import React from "react"
import {
    Stack,
    Flex,
    Button,
    Text,
    VStack,
    useBreakpointValue,
    Heading,
    Box,
  } from '@chakra-ui/react'

import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { graphql, Link as GatsbyLink } from 'gatsby'
import { isInternal } from "../../utils"
import slick from "slick-carousel/slick/slick.css"
import slick_theme from "slick-carousel/slick/slick-theme.css"
import Slider from "react-slick"
import "./css/itemSlider.css"

export const fragment = graphql`
  fragment ItemSliderBrand on WpBrand_Pagecomponents_PageComponents_ImageGrid {
            numberOfItemsToShow
            sectionSubtext
            sectionTitle
            fieldGroupName
            imageSection {
              buttonLabel
              fieldGroupName
              link
              subtext
              title
              image {
                altText
                localFile {
                  childImageSharp {
                    gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
                  }
                }
              }
            }
  }
`;

const ArrowStyles = (props) => {
    const { className, style, onClick } = props;
    return (
        <Box
          _before={{color: 'gray.800'}}
          className={className}
          style={{ ...style, display: "block" }}
          onClick={onClick}
        />
      )
}
const ItemSlider = ( data ) => {
  const imageSections = data.imageSection
  const title = data.sectionTitle;
  const subtitle = data.sectionSubtext
  const numberOfSlides = data.numberOfItemsToShow

  const settings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: numberOfSlides,
        slidesToScroll: numberOfSlides,
        initialSlide: 0,
        swipeToSlide: true,
        nextArrow: <ArrowStyles />,
        prevArrow: <ArrowStyles />,
        slide: <Flex />,
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 3,
              infinite: true,
              dots: true
            }
          },
          {
            breakpoint: 768,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              initialSlide: 2
            }
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1
            }
          }
        ]
      }

    
    const h1Breakpoints = useBreakpointValue({ base: '3xl', md: '5xl', lg: '7xl' })
    const h2Breakpoints = useBreakpointValue({ base: '2xl', md: '4xl', lg: '6xl' })
    const h3Breakpoints = useBreakpointValue({ base: 'xl', md: '2xl' })
    const h4Breakpoints = useBreakpointValue({ base: 'l', md: 'xl' })

      console.log(imageSections) 

      return (
        <Stack
        px={'40px'}
        py={[12,12,16,16]}
        bgColor='gray.50'
        textAlign="center"
        color={'gray.800'}
        >
          <Heading
            as={'h2'}
              color={'gray.800'}
              fontWeight={700}
              lineHeight={1.2}
              px={'12'}
              pb={'4'}
              mt={'0px !important'}
              textAlign={['left,', 'left','center','center']}
              fontSize={h2Breakpoints}>
              {title}
            </Heading>
            <Text
              px={'12'}
              pb={'10'}
              mt={'0px !important'}
              as = "h3"
              color={'gray.800'}
              fontWeight={500}
              lineHeight={1.2}
              textAlign={['left,', 'left','center','center']}
              fontSize={h3Breakpoints}>
              {subtitle}
            </Text>
          <Slider {...settings}>
          { imageSections?.map( (section, i) => {
            const link = section.link
            const isLinkInternal = isInternal(link)
            return(
            <VStack classname="test" key={i}
            p="3"
            height="100%"
            >
              <Box
              boxShadow="xl"
              rounded="md"
              bg="white"
              mx={3}
              pb={20}
              my={-6}
              h="100%"
              >
                <GatsbyImage imgStyle={{padding: '12px'}}  className="image" loading="lazy" image={getImage(section.image.localFile)} alt={section.image.altText} />
                <Box
                px={6}
                pt={6}
                height="auto !important"
                textAlign="left">
                  <Heading
                  as="h4"
                  mb="4"
                  fontSize={h4Breakpoints}>{section.title}</Heading>
                  <Text
                  mb="6"
                  >{section.subtext}</Text>
                  <Button
                    as={isLinkInternal ? GatsbyLink : "a"}
                    to={link}
                    href={link}
                    target={isLinkInternal ? "_self" : "_blank"}
                    bg={'#e55424'}
                    rounded={'xl'}
                    color={'white'}
                    size="lg"
                    _hover={{ bg: 'gray.800' }}>
                    {section.buttonLabel}
                  </Button>
                </Box>
              </Box>
            </VStack>
            )
          })}
          
          </Slider>
        </Stack>
      )
}

export default ItemSlider