import React from "react"
import Layout from "../components/Layout"
import HeroSide from "../components/brandComponents/HeroSide"
import Hero from "../components/brandComponents/Hero"
import { graphql } from 'gatsby'
import Seo from 'gatsby-plugin-wpgraphql-seo';
import PostGrid from "../components/brandComponents/PostGrid"
import ItemSlider from "../components/brandComponents/ItemSlider"
import Features from "../components/brandComponents/Features"


export const pageQuery = graphql`
query BrandById($id: String) {
  wpBrand(id: {eq: $id}) {
    id
    title
    uri
     pageComponents {
      fieldGroupName
      pageComponents {
        __typename
        ...HeroSide
        ...HeroComponentBrand
        ...ItemSliderBrand
      }
    }
    seo {
        title
        metaDesc
        focuskw
        metaKeywords
        metaRobotsNoindex
        metaRobotsNofollow
        opengraphTitle
        opengraphDescription
        opengraphImage {
            altText
            sourceUrl
            srcSet
            localFile {
                childImageSharp {
                    gatsbyImageData(layout: FIXED, placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
                }
            }
        }
        twitterTitle
        twitterDescription
        twitterImage {
            altText
            sourceUrl
            srcSet
            localFile {
                childImageSharp {
                    fixed {
                    src
                    }
                }
            }
        }
        canonical
        cornerstone
        schema {
            articleType
            pageType
            raw
        }
    }
  }
  ...PostItems
  ...NewsItems
  ...EventItems
}
`;


const Brand = ({ data }) => {
  const  brandComponents = data.wpBrand.pageComponents?.pageComponents;
  
  return (
    <Layout>
      <Seo post={data.wpBrand} />
      <div>
      { brandComponents?.map( (component, i) => {
        const typeName = component.__typename
        const key = data.wpBrand.id + typeName + i
        switch (typeName) {
          case 'WpBrand_Pagecomponents_PageComponents_HeroComponent':
            return (component.heroType === 'full' ? <Hero key={typeName+i} {...component}/> : <HeroSide key={key} {...component}/>)

          case 'WpBrand_Pagecomponents_PageComponents_ImageGrid':
            return <ItemSlider key={typeName+i} {...component}/>

          case 'WpBrand_Pagecomponents_PageComponents_FeaturesComponent':
            return <Features />
  
            default:
              return 
        }
      })}
    </div>
    </Layout>
  )
}

export default Brand