import React from "react"
import {
    Stack,
    Flex,
    Button,
    Text,
    VStack,
    useBreakpointValue,
    Heading,
    Box,
  } from '@chakra-ui/react'
  import { GatsbyImage, getImage } from "gatsby-plugin-image"
  import { graphql, Link as GatsbyLink } from 'gatsby'
  import { isInternal } from "../../utils"
  
  
  export const fragment = graphql`
  fragment HeroSide on WpBrand_Pagecomponents_PageComponents_HeroComponent {
          colorOverlay
          colorOverlayPercentage
          colorOverlayValue
          h1Heading
          heading
          subtext
          sectionHeight
          direction
          heroType
          fieldGroupName
          buttons {
            buttonIconClass
            buttonLabel
            buttonLink
          }
          backgroundImages {
            fieldGroupName
            image {
              altText
              id
              link
              sourceUrl
              localFile {
                childImageSharp {
                  gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
                }
              }
            }
          }
          heroInnerImage {
              altText
              id
              link
              sourceUrl
              localFile {
                childImageSharp {
                  gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
                }
              }
          }
        }
`


  const HeroSide = (data) => {
    const bgImages = data.backgroundImages
    const innerImage = data.heroInnerImage
    const overlay = data.colorOverlay
    const overlayValue = data.colorOverlayPercentage
    const overlayColor = data.colorOverlayValue
    const gradientColor = overlayColor+overlayValue
    const isH1 = data.h1Heading
    const title = data.heading
    const subtext = data.subtext
    const buttons = data.buttons
    const componentHeight = data.sectionHeight
    const primaryLink = buttons[0].buttonLink
    const secondaryLink = buttons[1]?.buttonLink
    const isPrimaryInternal = isInternal(primaryLink)
    const isSecondaryInternal = isInternal(secondaryLink)
    const hasImage = bgImages
    const h1Breakpoints = useBreakpointValue({ base: '3xl', md: '5xl', lg: '7xl' })
    const h2Breakpoints = useBreakpointValue({ base: '2xl', md: '4xl', lg: '6xl' })
    const h3Breakpoints = useBreakpointValue({ base: 'xl', md: '2xl' })
    const cardDirection = data.direction

    return (
      <Stack
        w={'full'}
        position="relative"
        overflow="hidden"
        h={['auto','auto',componentHeight,componentHeight]}
        flexDirection={["column","column","row","row"]}
        alignItems={'flex-end'}
        justifyContent={cardDirection}>
          {hasImage && (
            <Box
            minHeight="100vh"
            minWidth="100vw"
            position="absolute"
            top="0"
            left="0"
            zIndex="-1"
            >
            
            <GatsbyImage style={{minHeight: componentHeight , overflow: 'visible', position: 'relative'}} imgStyle={{minHeight: componentHeight}} loading="eager" className="hero-bg" image={getImage(bgImages[0].image.localFile)} alt={title} />
            </Box>
          )}
          <Flex
          justify={'center'}
          width={["100%","100%","100%","100%"]}
          pt={["25%","12%","0","0%"]}
          pb={["20%","8%","0","0%"]}
          height="100%"
          alignItems={'center'}
          >
            <GatsbyImage style={{height: 'auto' , width: '50%', overflow: 'visible', position: 'relative'}}  loading="eager"  image={getImage(innerImage.localFile)} alt={title} />
          </Flex>
        <VStack
          width="100%"
          maxW={["100vw","100vw","50vw","50vw"]}
          maxH="100%"
          justify={'center'}
          px={['2','2','2','0']}
          bgColor={["white","white","whiteAlpha.900","whiteAlpha.900"]}
          mx={['0px !important','0px !important','0px !important','32px !important']}
          boxShadow={'xl'}
          >
          
          <Stack py={[12,12,16,16]} position="relative" align={'flex-start'}  spacing={4}>
            <Box
            width="100%"
            minH="100%"
            bgColor="#ffffff50"
            zIndex="-1"
            left={cardDirection === 'flex-end' ? ['-10%','-15%','-9%','-9.5%'] : 'auto'}
            right={cardDirection === 'flex-start' ? ['-10%','-15%','-9%','-9.5%'] : 'auto'}
            top={['-8%','-10%','-16%','-16%']}
            position="absolute"
            />
            <Heading
            px={'12'}
            pb={'4'}
            mt={'0px !important'}
            as={isH1 ? 'h1' : 'h2'}
              color={!hasImage ? 'white' : '#2f2f2f'}
              fontWeight={900}
              lineHeight={1.2}
              textAlign='left'
              fontSize={isH1 ? h1Breakpoints : h2Breakpoints}>
              {title}
            </Heading>
            <Text
              px={'12'}
              pb={'6'}
              mt={'0px !important'}
              as = "h3"
              color={!hasImage ? 'white' : '#2f2f2f'}
              fontWeight={700}
              lineHeight={1.5}
              textAlign='left'
              fontSize={'h3Breakpoints'}>
              {subtext}
            </Text>
            
            <Stack px={'12'} alignItems={'flex-start'} width="100%" spacing={8} direction={'row'}>
              <Button
                as={isPrimaryInternal ? GatsbyLink : "a"}
                to={primaryLink}
                href={primaryLink}
                target={isPrimaryInternal ? "_self" : "_blank"}
                bg={'#e55424'}
                rounded={'xl'}
                color={'white'}
                size={"lg"}
                _hover={{ bg: 'gray.900' }}>
                {buttons[0].buttonLabel}
              </Button>
              {buttons[1] && (<Button
                as={isSecondaryInternal ? GatsbyLink : "a"}
                to={secondaryLink}
                href={secondaryLink}
                target={isSecondaryInternal ? "_self" : "_blank"}
                rounded={'xl'}
                variant="outline"
                colorScheme="orange"
                size={"lg"}
                _hover={{ bg: 'gray.900', color: 'white', border: '1px solid' }}
                >
                {buttons[1].buttonLabel}
              </Button>)}
            </Stack>
            
          </Stack>
        </VStack>
        
      </Stack>
    );
  }

  export default HeroSide